#page-transition {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    z-index: 9999;
    opacity: 1;
    pointer-events: none;
    transition: opacity 2s ease-out;
    transform: translateX(0);
  }
  
  #page-transition.active {
    opacity: 0;
    transform: translateX(-200%);
    pointer-events: auto;
  }
  